import React, { useEffect, useState } from 'react';
import client from '../../../../utils/prismicHelpers';
import * as queries from "../../../../utils/prismicQueries";
import AcademicCommoncrd from '../../AcademicCommoncrd/AcademicCommoncrd';
import CompletionStage from '../CompletionStage/CompletionStage';
const CallParticipation = () => {
    const [participateInfo, setParticipateInfo] = useState(null);


    useEffect(() => {
        client
            .query({
                query: queries.callPartipat,
            })
            .then((response) => {
                const paticipateDoc = response.data.allParticipations.edges[0].node;
                setParticipateInfo(paticipateDoc);


            })
            .catch((error) => {
                console.error(error);
            });
    });


    return (
        <div className="participation-container ">
            <h5 className="robot-challenge-title">{participateInfo?.sub_title1[0]?.text}</h5>
            <p className="body-text">{participateInfo?.sub_description1[0]?.text}</p>

            {participateInfo?.stage?.map(stage => <CompletionStage stage={stage}></CompletionStage>)}

            <div className="row g-2 pt-5">
                {participateInfo?.card?.map(info =>
                    <AcademicCommoncrd
                        info={info}
                        crdHight={420}
                    >

                    </AcademicCommoncrd>)
                }


            </div>
            <div className="participation-footer pt-5">
                <h5>{participateInfo?.email_text[0]?.text} <a href="#">{participateInfo?.email[0]?.text}</a></h5>

                <p className="body-text">{participateInfo?.email_text_description[0]?.text}</p>
            </div>


        </div>
    );
};

export default CallParticipation;