import React, { Component } from "react";
import Slider from "react-slick";
import TeamCard from "../TeamCard/TeamCard";
import client from "../../../utils/prismicHelpers";
import * as queries from "../../../utils/prismicQueries";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowLeft, faArrowRight } from "@fortawesome/free-solid-svg-icons";
export default class TeamSlider extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sliders: null,
      newSlider: null,
      sliderBtn: false,
      activeSlide2: 0,
      activeSlide: 0,
      hasLoaded: false
    };
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);


  }

  listenScrollEvent(event) {

  }


  componentDidMount() {
    client.query({ query: queries.teamSlider })
      .then((response) => {
        const sliderInfo = response.data.allTeams.edges[0].node.mamber_info;

        this.setState({ sliders: sliderInfo, hasLoaded: true })
        this.setState({ newSlider: sliderInfo })
        setTimeout(() => {
          this.slider.slickGoTo(0);
        }, 1000);
      })
      .catch((error) => {
        console.error(error);
      });
  }
  next() {
    this.slider.slickNext();
    this.setState({ sliderBtn: true })

  }

  previous() {
    this.slider.slickPrev();
  }
  render() {
    const settings = {
      className: "center",
      dots: true,
      infinite: false,
      centerPadding: "60px",
      slidesToShow: 3,
      speed: 500,
      rows: 2,
      slidesToScroll: 3,
      slidesPerRow: 1,

      beforeChange: (current, next) => this.setState({ activeSlide: next }),
      afterChange: current => this.setState({ activeSlide2: current * 2 + 6 }),
      responsive: [
        {
          breakpoint: 400,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            dots: true,
            rows: 3,

            afterChange: current => this.setState({ activeSlide2: current * 2 + 6 }),
          },
        },
        {
          breakpoint: 300,
          settings: {
            dots: true,

          },
        },
        {
          breakpoint: 800,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 2,
            dots: true,
          },
        },
      ],
    };



    return (
      this.state.hasLoaded ? <div className="team-slider">

        <Slider ref={(c) => (this.slider = c)} {...settings}>
          {this.state.sliders?.map((teamInfo) => (
            <TeamCard
              key={Date.now() + Math.random()}
              teamInfo={teamInfo}
            ></TeamCard>
          ))}
        </Slider>
        <div className="d-flex justify-content-between pt-3 ">
      
          <button
            className={`${this.state.activeSlide === 0 ? "  disable-sldr-btn" : "  active-sldr-btn"}  `}
            onClick={this.previous}>
            <FontAwesomeIcon icon={faArrowLeft} />
          </button>

          <button className={` ${this.state.activeSlide2 === this.state.sliders.length ? " disable-sldr-btn" :
            this.state.activeSlide2 === this.state.sliders.length + 1 ? " disable-sldr-btn" :
              " active-sldr-btn"} `}
            onClick={this.next}>
            <FontAwesomeIcon icon={faArrowRight} />
          </button>
        </div>
      </div> : null
    );
  }
}
