import { faTwitter, faLinkedinIn, faFacebookF, faYoutube, faInstagram, faWhatsapp } from '@fortawesome/free-brands-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useEffect, useState } from 'react';
import client from '../../utils/prismicHelpers';
import * as queries from "../../utils/prismicQueries";
const SocialLinks = ({ showWhatsApp }) => {

    const [social, setSocial] = useState(null);


    useEffect(() => {
        client
            .query({
                query: queries.contact,
            })
            .then((response) => {
                const socialLinks = response.data.allContacts.edges[0].node;
                setSocial(socialLinks);


            })
            .catch((error) => {
                console.error(error);
            });
    });

    return (
        <div className=" d-flex justify-content-between text-secondary">
            <a target="_blank" href={social?.socilal[0].twitter[0]?.text}>  <FontAwesomeIcon icon={faTwitter} /></a>
            <a target="_blank" href={social?.socilal[0].linkedin[0]?.text}>  <FontAwesomeIcon icon={faLinkedinIn} /></a>
            <a target="_blank" href={social?.socilal[0].fb[0]?.text}>  <FontAwesomeIcon icon={faFacebookF} /></a>
            <a target="_blank" href={social?.socilal[0].youtube[0]?.text}>  <FontAwesomeIcon icon={faYoutube} /></a>
            <a target="_blank" href={social?.socilal[0].instragram[0]?.text}>  <FontAwesomeIcon icon={faInstagram} /></a>
            {showWhatsApp && <a href="https://web.whatsapp.com/send?text=https://art-park-b4155.web.app" target="_blank">  <FontAwesomeIcon icon={faWhatsapp} /></a>}
        </div>
    );
};

export default SocialLinks;