import React, { Component } from "react";
import Slider from "react-slick";
import client from "../../utils/prismicHelpers";
import * as queries from "../../utils/prismicQueries";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowLeft, faArrowRight, } from '@fortawesome/free-solid-svg-icons'
import HealthGroupImg from "./HealthGroupImg";
import HealthCard from "./HealthCard";

export default class HealthSlider extends Component {
    constructor(props) {
        super(props);
        this.state = { sliders: [], sliderBtn: false, activeSlide2: 0, activeSlide: 1 };
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);

    }
    componentDidMount() {
        client.query({ query: queries.health })
            .then((response) => {
                const sliderInfo = response.data.allHealths.edges[0].node.health_slider;
                this.setState({ sliders: sliderInfo })


            })
            .catch((error) => {
                console.error(error);
            });
    }

    next() {
        this.slider.slickNext();
        this.setState({ sliderBtn: true })

    }
    previous() {
        this.slider.slickPrev();
    }
    render() {
        const settings = {
            className: "center",
            infinite: false,
            centerPadding: "60px",
            slidesToShow: 1,
            speed: 500,
            rows: 1,
            slidesPerRow: 1,
            beforeChange: (current, next) => this.setState({ activeSlide: next }),
            afterChange: current => this.setState({ activeSlide2: current * 2 + 6 }),
           
        };


        return (
            this.state.sliders.length != 0 ? <div className="health-slider">

                <div className="row g-5 ">
                    <div className="col-md-6">
                        <Slider ref={c => (this.slider = c)} {...settings}>
                            {
                                this.state.sliders?.map(slider => <HealthCard healthInfo={slider} sliderCard={true}/>)
                                
                            }

                        </Slider>
                        <div className='d-flex justify-content-md-around justify-content-between   pt-3'>

                            <button
                                className={`${this.state.activeSlide === 0 ? "  disable-sldr-btn" : "  active-sldr-btn"}  `}
                                onClick={this.previous}>
                                <FontAwesomeIcon icon={faArrowLeft} />
                            </button>
                            {this.state.activeSlide}/{this.state.sliders.length}

                            <button className={` ${this.state.activeSlide2 === this.state.sliders.length ? " disable-sldr-btn" :
                                this.state.activeSlide2 === this.state.sliders.length + 1 ? " disable-sldr-btn" :
                                    " active-sldr-btn"} `}
                                onClick={this.next}>
                                <FontAwesomeIcon icon={faArrowRight} />
                            </button>

                        </div>
                    </div>
                    <div className="col-md-6 d-none d-md-block">
                        <div className="row g-2 px-3 pb-3">
                            {this.state.sliders?.map(sliderImg=><HealthGroupImg sliderImg={sliderImg}></HealthGroupImg>)}
                        </div>
                    </div>
                </div>


            </div> : null
        );
    }
}
