import React, { Component } from "react";
import Slider from "react-slick";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faArrowLeft, faArrowRight } from "@fortawesome/free-solid-svg-icons";
export default class NewsSlider extends Component {
  constructor(props) {
    super(props);
    this.state = {
      sliders: [],
      sliderBtn: false,
      activeSlide2: 0,
      activeSlide: 0,
    };
    this.next = this.next.bind(this);
    this.previous = this.previous.bind(this);
  }
  next() {
    this.slider.slickNext();
    this.setState({ sliderBtn: true })


  }
  previous() {
    this.slider.slickPrev();
  }
  render() {
    const settings = {
      className: "center",
      focusOnSelect: true,

      dots: true,
      infinite: false,
      centerPadding: "60px",
      slidesToShow: 3,
      speed: 500,
      rows: 1,
      slidesPerRow: 1,
      beforeChange: (current, next) => this.setState({ activeSlide: next }),
      afterChange: current => this.setState({ activeSlide2: current + 3 }),

 
      responsive: [


        {
          breakpoint: 480,
          settings: {
            slidesToShow: 1,
            slidesToScroll: 1,
            centerMode: false,
            afterChange: current => this.setState({ activeSlide2: current + 1 }),
            dots: true,
          }
        },
        {
          breakpoint: 800,
          settings: {
            slidesToShow: 2,
            slidesToScroll: 1,
            dots: true,

          }
        },
      ]
    };

    return (
      <div className="news-slider">
        <Slider ref={c => (this.slider = c)} {...settings}>
          {
            this.props?.newsSlider?.map(article => {
              return (
                <div className="row" key={Date.now() + Math.random()}>
                  <div className="col-md-12">
                    <a target="_blank" className="text-decoration-none text-black" href={article?.external_link[0]?.text}>
                      <div className="slider-card">
                        <p className="section-body ">{

                          article?.description1[0]?.text.length > 70 ? ` ${article?.description1[0]?.text.slice(0, 70)} .....` : article?.description1[0]?.text}</p>
                        <div className="d-flex ">
                          <div className="news-logo pe-2"> <img src={article?.logo?.url} alt="" className="img-fluid max28" /></div>
                          <p>{article?.title1[0]?.text}</p>

                        </div>
                      </div>
                    </a>
                  </div>
                </div>
              )
            })
          }

        </Slider>


        <div className='d-flex justify-content-md-around justify-content-between   pt-3'>
          <button 
          className={`${this.state.activeSlide === 0 ? " disable-sldr-btn" : " active-sldr-btn"}  `}
           onClick={this.previous}>

            <FontAwesomeIcon icon={faArrowLeft} />
          </button>

          <button
           className={` ${this.state.activeSlide2  === this.props?.newsSlider.length ? " disable-sldr-btn"  : " active-sldr-btn"}`}
            onClick={this.next}>

            <FontAwesomeIcon icon={faArrowRight} />
          </button>
         

        </div>
      </div>
    );
  }
}

