import React, { useEffect, useState } from "react";
import "./home.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronDown } from "@fortawesome/free-solid-svg-icons";
import Emitter from "../../utils/emitter";
import * as queries from "../../utils/prismicQueries";
import client from "../../utils/prismicHelpers";


const HomeSection = () => {
  const [homeDoc, setHomeDoc] = useState(null);
  useEffect(() => {
    client
      .query({
        query: queries.heroSection,
      })
      .then((response) => {
        const homeDocContent = response.data.allHomes.edges[0].node;
        setHomeDoc(homeDocContent);
      })
      .catch((error) => {
        // console.error(error);
      });
    // Emitter.on("TOGGLE_MENU", (newValue) => setToggle(!toggled));
  });


  

  function moveDown() {
    Emitter.emit("MOVE_DOWN", "DOWN");
  }

  return (
    <div style={{ backgroundImage: `url(${homeDoc?.image?.url})` }} className="container-fluid home-section">
      <div className="container">
 
        <div className="heading-home">
          {homeDoc?.heading[0]?.text}
        </div>
      </div>
      <div className="ScrollDown">
        <div className="text-center text-white">
          <FontAwesomeIcon
            icon={faChevronDown}
            className="down-icon"
            size="xs"
            onClick={() => moveDown()}
          />
        </div>
      </div>
    </div>
  );
};

export default HomeSection;
