import React ,{useState,useEffect}from 'react';
import AboutCard from '../AboutCard/AboutCard';
import client from '../../../utils/prismicHelpers';
import * as queries from "../../../utils/prismicQueries";

const AboutInfo = () => {
   
    const [aboutInfo, setAboutInfo] = useState(null);

    useEffect(() => {
        client
            .query({
                query: queries.aboutInfo,
            })
            .then((response) => {
                const aboutDocContent = response.data.allAbouts.edges[0].node;
                setAboutInfo(aboutDocContent.about_info);

            })
            .catch((error) => {
                console.error(error);
            });
    });

  
    return (
        <div className="d-flex justify-content-start">
            <div className="row gy-2">
                {aboutInfo?.map(aboutInfo=> <AboutCard info={aboutInfo} key={Date.now() + Math.random()}></AboutCard>)}
            </div>
        </div>
    );
};

export default AboutInfo;