import React, { useEffect, useState } from 'react';
import InvestmentCommittee from '../InvestmentCommittee/InvestmentCommittee';
import InnovationMain from '../InnovationMain/InnovationMain';
import './artParkDetails.css'
import Cohorts from '../Cohorts/Cohorts';
import client from '../../../utils/prismicHelpers';
import * as queries from "../../../utils/prismicQueries";
import InnoFellow from '../InnoFellow/InnoFellow';
import VentureFund from '../VentureFund/VentureFund';

const ArtParkDetails = () => {
    const [innoBg, setInnoBg] = useState(null);
    useEffect(() => {
        client
            .query({
                query: queries.innovationBg,
            })
            .then((response) => {
                const innovationContent = response.data.allInnoprograms.edges[0].node;
                setInnoBg(innovationContent);
            })
            .catch((error) => {
                console.error(error);
            });
    });

    function rebuildFP() {
        // setTimeout(() => {
            console.re.log("REBUILDING FP")
          window.fullpage_api.reBuild()
        // }, 700);
    }

    return (
        <section style={{ backgroundImage: `url(${innoBg?.section_bg?.url})` }} className="innovation-container pattern">
            <div id="art-stepper" className="container  stepper" >
                    <div className="nav nav-tabs" id="nav-tab" role="tablist">
                        <button className="nav-link border-0 active primary-text" id="nav-home-tab" data-bs-toggle="tab" data-bs-target="#nav-home" type="button" role="tab" aria-controls="nav-home" aria-selected="true"  onClick={() => rebuildFP()}>INNOVATION PROGRAM</button>
                        <button className="nav-link border-0 primary-text" id="nav-profile-tab" data-bs-toggle="tab" data-bs-target="#nav-profile" type="button" role="tab" aria-controls="nav-profile" aria-selected="false" onClick={() => rebuildFP()}>INVESTMENT COMMITTEE</button>
                        <button className="nav-link  border-0 primary-text" id="nav-cohorts-tab" data-bs-toggle="tab" data-bs-target="#nav-cohorts" type="button" role="tab" aria-controls="nav-cohorts" aria-selected="false" onClick={() => rebuildFP()}>COHORTS</button>
                        <button className="nav-link  border-0 primary-text" id="innovation-fellow-tab" data-bs-toggle="tab" data-bs-target="#innovation-fellow" type="button" role="tab" aria-controls="innovation-fellow" aria-selected="false" onClick={() => rebuildFP()}> INNOVATION FELLOW </button>
                        <button className="nav-link  border-0 primary-text" id="venture-fund-tab" data-bs-toggle="tab" data-bs-target="#venture-fund" type="button" role="tab" aria-controls="venture-fund" aria-selected="false" onClick={() => rebuildFP()}> VENTURE FUND</button>

                    </div>

                    <div className="tab-content" id="nav-tabContent">
                        <div className="tab-pane fade show active " id="nav-home" role="tabpanel" aria-labelledby="nav-home-tab">
                            <InnovationMain ></InnovationMain>
                        </div>
                        <div className="tab-pane fade" id="nav-profile" role="tabpanel" aria-labelledby="nav-profile-tab">
                            <InvestmentCommittee></InvestmentCommittee>
                        </div>
                        <div className="tab-pane fade" id="nav-cohorts" role="tabpanel" aria-labelledby="nav-cohorts-tab">
                            <Cohorts></Cohorts>
                        </div>
                        <div className="tab-pane fade" id="innovation-fellow" role="tabpanel" aria-labelledby="innovation-fellow-tab">
                            <InnoFellow></InnoFellow>
                        </div>
                        <div className="tab-pane fade" id="venture-fund" role="tabpanel" aria-labelledby="venture-fund-tab">
                            <VentureFund></VentureFund>
                        </div>
                    </div>
                </div>
            
        </section>

    );
};

export default ArtParkDetails;