import React, { Component } from "react";
import Slider from "react-slick";
import CareerModal from "../../career/CareerModal/CareerModal";
import client from "../../../utils/prismicHelpers";
import * as queries from "../../../utils/prismicQueries";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faArrowLeft, faArrowRight, } from '@fortawesome/free-solid-svg-icons'
import CareerCard from "./CareerCard";

export default class CareerSlider extends Component {
    constructor(props) {
        super(props);
        this.state = {
            sliders: [],
            sliderBtn: false,
            activeSlide2: 0,
            activeSlide: 0,
            openModal: true,
        };
        this.next = this.next.bind(this);
        this.previous = this.previous.bind(this);

    }
    componentDidMount() {
        client.query({ query: queries.careerSlider })
            .then((response) => {
                const sliderInfo = response.data.allCareer_sliders.edges;
                this.setState({ sliders: sliderInfo })


            })
            .catch((error) => {
                console.error(error);
            });
    }

    next() {
        this.slider.slickNext();
        this.setState({ sliderBtn: true })

    }
    previous() {
        this.slider.slickPrev();
    }
    render() {
        const settings = {
            className: "center",
            dots: true,
            infinite: false,
            centerPadding: "60px",
            slidesToShow: 3,
            speed: 500,
            rows: 2,
            slidesPerRow: 1,
            beforeChange: (current, next) => this.setState({ activeSlide: next }),
            afterChange: current => this.setState({ activeSlide2: current * 2 + 6 }),
            responsive: [
                {
                    breakpoint: 480,
                    settings: {
                        slidesToShow: 1,
                        slidesToScroll: 1,
                        dots: true,
                        centerMode: false,
                        rows: 3,
                        afterChange: current => this.setState({ activeSlide2: current * 3 + 3 }),
                    }
                },
                {
                    breakpoint: 1200,
                    settings: {
                        slidesToShow: 2,
                        slidesToScroll: 1,
                        dots: true,
                        rows: 3,

                    }
                },
            ]
        };


        return (
            this.state.sliders.length != 0 ? <div className="career-slider">

                <Slider ref={c => (this.slider = c)} {...settings}>
                    {
                        this.state.sliders?.map(slider => <CareerCard slider={slider}/>

                         
                                // <div className="row  " key={Date.now() + Math.random()}>
                                //     <div className="col-md-12 g-2">
                                //         <div
                                //          onClick={()=> this.setState({ openModal: !this.state.openModal })} 
                                //          className='career-card pb-0 '>

                                //             <p style={{ fontWeight: 700 }} className='primary-text'>{slider?.node.designation[0]?.text}</p>
                                //             <div className="d-flex ">
                                //                 <div className=" d-flex  flex-column flex-grow-1   pe-2">

                                //                     <div className="d-flex  flex-md-column   flex-sm-row justify-content-between ">
                                //                         <p className='primary-text '>{slider?.node.location[0]?.text}</p>
                                //                         <p className='primary-text '>{slider?.node.job_type[0]?.text}</p>
                                //                     </div>
                                //                 </div>

                                //                 <div className="d-flex ">

                                //                     <div className=" align-self-end carrer-sld-btn  pb-0">
                                //                         <CareerModal modalInfo={slider?.node} openModal ={this.state?.openModal} />

                                //                     </div>
                                //                 </div>
                                //             </div>

                                //         </div>
                                //     </div>
                                // </div>
                            
                        )
                    }

                </Slider>

                <div className='d-flex justify-content-md-around justify-content-between   pt-3'>

                    <button
                        className={`${this.state.activeSlide === 0 ? "  disable-sldr-btn" : "  active-sldr-btn"}  `}
                        onClick={this.previous}>
                        <FontAwesomeIcon icon={faArrowLeft} />
                    </button>

                    <button className={` ${this.state.activeSlide2 === this.state.sliders.length ? " disable-sldr-btn" :
                        this.state.activeSlide2 === this.state.sliders.length + 1 ? " disable-sldr-btn" :
                            " active-sldr-btn"} `}
                        onClick={this.next}>
                        <FontAwesomeIcon icon={faArrowRight} />
                    </button>

                </div>
            </div> : null
        );
    }
}
