
import React,{useEffect,useState} from 'react';
import client from '../../utils/prismicHelpers';
import * as queries from "../../utils/prismicQueries";


function Logo() {
  const [logo,setLogo] = useState(null);


  useEffect(() => {
      client
          .query({
              query: queries.navLogo,
          })
          .then((response) => {
              const logoInfo = response.data.allNavbars.edges[0].node;
              setLogo(logoInfo);
         

          })
          .catch((error) => {
              console.error(error);
          });
  });

  return (
    <div className="logoBox d-flex ">
      <img src={logo?.pirmary_logo?.url} alt="" className="img-fluid me-auto m9log"></img>
      <div>
        <img src={logo?.secondary_logo?.url} alt="" className="img-fluid m13log"></img>
      </div>
    </div>
  );
}

export default Logo;



