import React, { useEffect, useState } from 'react';
import client from '../../utils/prismicHelpers';
import * as queries from "../../utils/prismicQueries";
import FullMenu from '../../main/components/fullmenu';
import Header from '../../main/components/header';
import './health.css'
import HealthCard from './HealthCard';
import Footer from '../footer/Footer/Footer';
import SocialLinks from '../../main/components/socialLinks';
import { ValuesOfCorrectTypeRule } from 'graphql';
import HealthSlider from './HealthSlider';
import HealthTopCrd from './HealthTopCrd/HealthTopCrd';
import {Helmet} from "react-helmet";

const ReimagineHealth = () => {


    const [healthInfo, setHealthDoc] = useState(null);


    useEffect(() => {
        client
            .query({
                query: queries.health,
            })
            .then((response) => {
                const healthInfo = response.data.allHealths.edges[0].node;
                setHealthDoc(healthInfo);


            })
            .catch((error) => {
                console.error(error);
            });
    });

    return (
        <>
            <Helmet>
                <meta charSet="utf-8" />
                <title>REIMAGINE HEALTH</title>
            </Helmet>
            <Header blackToggle={true} />
            <section
                className=" pb-0 section-common-info"
            >
                <div className="container">


                    <div className="  d-flex flex-column-reverse  flex-md-row  pb-2 justify-content-between">
                        <div className="w-75 health-header">
                            <p className=" fw-bold pb-0 px-md-1  health-fst-text">{healthInfo?.subtitle}</p>
                            <h3 className="subtitle  pt-0 ">{healthInfo?.title}</h3>

                            <div className=" health-description">
                                <p > {healthInfo?.des1}</p>
                                <p > {healthInfo?.des2}</p>
                                <p > {healthInfo?.des3}</p>
                            </div>

                        </div>

                        <HealthTopCrd ifHealthCrd={true} />


                    </div>
                    <div className="row py-2 g-1">

                        {healthInfo?.health_slider?.map((healthInfo, index) => <HealthCard healthInfo={healthInfo} key={index + 1}></HealthCard>)}
                    </div>

                    <div className="health-contact mb-5">

                        <div className='pt-5'>
                            <h3 >{healthInfo?.card2Des1}</h3>


                            <a
                                target="_blank"
                                className="text-decoration-none  "
                                href={"mailto:" + healthInfo?.card2_email}
                            ><h3>{healthInfo?.card2_email}</h3></a>

                        </div>

                    </div>




                </div>

            </section>
            <div className="health-footer    ">
                <Footer >
                    <p className="section-title p-0 m-0  pe-2 ">{healthInfo?.footer_text}</p>
                    <SocialLinks showWhatsApp={true} />
                </Footer>
            </div>
            <FullMenu />
        </>
    );
};

export default ReimagineHealth;