import React, { useEffect, useState } from 'react';
import client from '../../../../utils/prismicHelpers';
import * as queries from "../../../../utils/prismicQueries";

const Guidelines = () => {

    const [guideLines, setGuideInfo] = useState(null);


    useEffect(() => {
        client
            .query({
                query: queries.guideLines,
            })
            .then((response) => {
                const guideDoc = response.data.allGis.edges[0].node;
                setGuideInfo(guideDoc);


            })
            .catch((error) => {
                console.error(error);
            });
    });

    return (
        <div>
            <h3 className="subtitle ">{guideLines?.tilte[0]?.text}</h3>
            <ul>
                {guideLines?.description_list?.map(description => <li className="body-text pt-2">{description.para}</li>)}
            </ul>
            <div className="scroing pt-4">
                <h5 className="robot-challenge-title">{guideLines?.sub_title[0]?.text}</h5>
                <p className="body-text">{guideLines?.sub_des[0]?.text}</p>

                <ol>
                   {guideLines?.scoring?.map((listInfo, index) =><li className="body-text"> <span>{index + 1}.</span> {listInfo?.desc[0]?.text}</li>)}
               </ol>
  
       
                <h5 className="robot-challenge-title">{guideLines?.footer_title[0]?.text}</h5>
                <p className="body-text">{guideLines?. footer_des[0]?.text}</p>


            </div>

        </div>
    );
};

export default Guidelines;