import React, { useEffect, useState } from 'react';
import client from '../../../utils/prismicHelpers';
import * as queries from "../../../utils/prismicQueries";
import './footer.css'
const Footer = ({ children }) => {


    const [footer, setFooter] = useState(null);


    useEffect(() => {
        client
            .query({
                query: queries.footer,
            })
            .then((response) => {
                const footerLink = response.data.allFooters.edges[0].node;
                setFooter(footerLink);


            })
            .catch((error) => {
                console.error(error);
            });
    });


    return (
        <footer className="footer-container">
            <div className="container  py-0">
                <div className="d-flex links align-items-center">
                    {children ? children :
                        <>
                            
                            <a href={footer?.footer_link[0].link[0]?.text} className="muted-test me-auto m-0">{footer?.footer_link[0].link_text[0]?.text}</a>
                            <a target="_blank" href={footer?.footer_link[1].doc?.url} className=" m-0 primary-text pe-4">{footer?.footer_link[1].link_text[0]?.text}</a>
                            <a target="_blank" href={footer?.footer_link[2].doc?.url} className=" m-0 primary-text">{footer?.footer_link[2].link_text[0]?.text}</a></>
                    }
                </div>
            </div>
        </footer>
    );
};

export default Footer;