import React, { useEffect, useState } from 'react';
import client from '../../utils/prismicHelpers';
import * as queries from "../../utils/prismicQueries";
import './chart.css'
import ChartList from './ChartList';
const OurChart = () => {

    const [chartDoc, setChartDoc] = useState(null);


    useEffect(() => {
        client
            .query({
                query: queries.ourChart,
            })
            .then((response) => {
                const chartInfo = response.data.allOur_charters.edges[0].node;
                setChartDoc(chartInfo);


            })
            .catch((error) => {
                console.error(error);
            });
    });
    return (
        <section style={{ backgroundImage: 'url(' + chartDoc?.section_bg?.url + ')' }} className="pattern chart-container">
            <div className="container">
           
                <div  >
                    <h3 className="subtitle ">{chartDoc?.title}</h3>
                    <p className="body-text pt-2">{chartDoc?.description}</p>
                </div>
                <div>
                    {
                        chartDoc?.charter_info?.map((chartInfo ,index)=> <ChartList key={index+1}  chartInfo={chartInfo} />)
                    }

                </div>
            </div>

        </section>
    );
};

export default OurChart;