import { useState, useEffect } from 'react';
import Emitter from '../../utils/emitter';

function Menu({blackToggle}) {
  const [colorText, setCount] = useState(1);
  const [toggle, setToggle] = useState(1);
  useEffect(() => {
    Emitter.on('SECTION_SCROLLED', (newValue) => setCount(newValue !== 0 ? 0 : 1));
    Emitter.on('TOGGLE_MENU', (newValue) => setToggle(!toggle));
  });

  function menuToggle() {
    setToggle(!toggle)

    Emitter.emit('TOGGLE_MENU', 'TOGGLE');
  }

  return (
    <div className={`${toggle ? '' : 'd-none'} ${colorText ? 'menuBox text-white' : 'menuBox text-blue'}`} onClick={() => menuToggle()}>
      <span className="navBox d-none d-md-block">
        <div className={colorText && !blackToggle ? 'navLine navLine-white' : 'navLine navLine-black'}></div>
        <div className={colorText && !blackToggle ? 'navLine navLine-white' : 'navLine navLine-black'}></div>
        <div className={colorText && !blackToggle ? 'navLine navLine-white' : 'navLine navLine-black'}></div>
      </span>
      <span className="navBox d-block d-sm-none">
        <div className= 'navLine mobile-toggle'></div>
        <div className= 'navLine mobile-toggle'></div>
        <div className= 'navLine mobile-toggle'></div>
      </span>
      <span className={`d-none d-sm-block ${blackToggle&& "text-black"}`}> MENU</span>
    </div>
  );
}

export default Menu;