import React, { useEffect, useState } from 'react';
import client from '../../../utils/prismicHelpers';
import * as queries from "../../../utils/prismicQueries";
import FullMenu from '../../../main/components/fullmenu';
import Header from '../../../main/components/header';
import './reimaginJourney.css'
import { useNavigate } from 'react-router';
import Footer from '../../footer/Footer/Footer';
import SocialLinks from '../../../main/components/socialLinks';
import HealthTopCrd from '../../health/HealthTopCrd/HealthTopCrd';
import HealthGroupImg from '../HealthGroupImg/HealthGroupImg';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faChevronRight } from '@fortawesome/free-solid-svg-icons';
import ProfileCrd from '../ProfileCrd/ProfileCrd';
import {Helmet} from "react-helmet";


const ReimaginJourney = () => {

    const navigate = useNavigate();
    const [healthInfo, setHealthDoc] = useState(null);
    const [healthImg, setHealthImg] = useState(null);


    useEffect(() => {
        client
            .query({
                query: queries.reimaginJourney,
            })
            .then((response) => {
                const healthInfo = response.data.allReimagin_journeys.edges[0].node;
                const healthImg = response.data.allHealths.edges[0].node.health_slider;
                setHealthDoc(healthInfo);
                setHealthImg(healthImg);


            })
            .catch((error) => {
                console.error(error);
            });
    });

    return (
        <>
            <Helmet>
                <meta charSet="utf-8" />
                <title>REIMAGINE HEALTH</title>
            </Helmet>
            <Header blackToggle={true} />
            <section
                className=" pb-0 health-pattern  section-common-info pattern"
            >
                <div className="container">


                    <div className="  d-flex flex-column-reverse  flex-md-row  pb-2 justify-content-between">
                        <div className="w-75 health-header">

                            <h3 className="subtitle  pt-0 ">{healthInfo?.title}</h3>

                            <div className="w-75">
                                <p >{healthInfo?.des1}
                                   
                                </p>
                                <p >
                                    <span className="fw-bold">{healthInfo?.des2?.slice(0, 27)}</span>
                                    {healthInfo?.des2?.slice(27, 170)}
                                    <span className="fw-bold">  {healthInfo?.des2?.slice(170, 204)}</span>
                                    {healthInfo?.des2?.slice(204, 2000)}

                                </p>

                                <p> {healthInfo?.read_more_title.slice(0, 20)}
                                    <span
                                        className="fw-bold text-primary  text-decoration-underline"
                                        onClick={() => navigate("/reimagine-health/2030_rural_india")}
                                    >{healthInfo?.read_more_title.slice(20, 53)}</span>
                                   {", "} {healthInfo?.read_more_title.slice(55, 100)}

                                </p>

                                <p >
                                    <a href={"mailto:" + healthInfo?.contact_text4}  >
                                        <span className="pe-1" >
                                            {healthInfo?.contact_text1}</span>
                                    </a>
                                    {healthInfo?.contact_text2.slice(0, 87)}
                                    <span className='fw-bold'>
                                        {healthInfo?.contact_text2.slice(87, 91)}
                                    </span>
                                    {healthInfo?.contact_text2.slice(91, 200)}
                                </p>

                            </div>

                        </div>

                        <HealthTopCrd />

                    </div>


                    <div className="row">

                        {healthInfo?.profile_info?.map((healthInfo, index) => <ProfileCrd profileInfo={healthInfo} key={index + 1}></ProfileCrd>)}
                    </div>


                </div>
            </section>
            <div className="health-footer    ">
                <Footer >
                    <p className="footer-text p-0 m-0  pe-2 ">{healthInfo?.share_text}</p>
                    <SocialLinks showWhatsApp={true} />
                </Footer>
            </div>
            <FullMenu />
        </>
    );
};

export default ReimaginJourney;

