import React,{useEffect,useState} from 'react';
import './newsSection.css'
import NewsMain from '../NewsMain/NewsMain';
import NewsSlider from '../NewsSlider/NewsSlider';
import client from '../../../utils/prismicHelpers';
import * as queries from "../../../utils/prismicQueries";
const NewsSection = () => {
 
    const [newsSlider,setNewsSlider] = useState(null);
    const [newsContent,setNewsContent] = useState(null);
  
    useEffect(() => {
        client
            .query({
                query: queries.news,
            })
            .then((response) => {
                const partnersContent = response.data.allNewss.edges[0].node;
                setNewsContent(partnersContent);
                setNewsSlider(partnersContent.slider_info);

            })
            .catch((error) => {
                console.error(error);
            });
    });

    return (
        <section style={{backgroundImage: `url(${newsContent?.news_bg?.url})`}} className='news-container '>
            <div id="news-stepper" className="container stepper "> 
                <NewsMain newsContent={newsContent}></NewsMain>
               {newsSlider != null ? <NewsSlider newsSlider={newsSlider}></NewsSlider> : null }
              
             
            </div>
        </section>

    );
};

export default NewsSection;